/*ORDER-STEPS
-------------------------------------------------*/
.site_order-steps {
  padding-bottom: 30px;
}
.site_order-steps__item {
  max-width: 300px;
  padding: 30px 0;
  margin: 0 auto 80px;
  text-align: center;
  position: relative;
}
.site_order-steps__item:before,
.site_order-steps__item:after {
  content: '';
  position: absolute;
  bottom: 100%;
  right: 50%;
  height: 52px;
  margin-bottom: 12px;
  transform: rotate(90deg);
}
.site_order-steps__item:before {
  width: 52px;
  margin-right: -26px;
  background: url(img/order-steps-arrow.png);
}
.site_order-steps__item:after {
  width: 29px;
  margin-right: -15px;
  background: url(img/order-steps-arrow-single.png);
  animation-name: orderStepsArrowBottom;
  animation-duration: 3s;
  animation-iteration-count: infinite;
  animation-timing-function: steps(4, end);
}
.site_order-steps__col-1 .site_order-steps__item:before,
.site_order-steps__col-1 .site_order-steps__item:after {
  content: none;
}
.site_order-steps__icon {
  width: 90px;
  height: 90px;
  line-height: 90px;
  margin: 0 auto 10px;
  font-size: 50px;
  color: #74b6f4;
  text-align: center;
}
.site_order-steps__text {
  font-size: 18px;
  color: #929292;
}
.site_order-steps__col-4 .site_order-steps__item {
  margin-bottom: 0;
}
.site_order-steps__col-4 .site_order-steps__icon {
  color: #dd6b2c;
  border: 2px solid #dd6b2c;
  border-radius: 100%;
}
.site_order-steps__col-4 .site_order-steps__text {
  color: #dd6b2c;
}
@media (min-width: 768px) {
  .site_order-steps__item {
    margin: auto;
    padding: 60px 0;
  }
  .site_order-steps__item:before,
  .site_order-steps__item:after {
    top: 100px;
    right: 100%;
    margin: 0;
    transform: none;
  }
  .site_order-steps__item:after {
    animation-name: orderStepsArrowRight;
  }
  .site_order-steps__icon {
    width: 150px;
    height: 150px;
    line-height: 125px;
    margin-bottom: 30px;
    font-size: 72px;
  }
}
@keyframes orderStepsArrowBottom {
  from {
    margin-bottom: 23px;
  }
  to {
    margin-bottom: -8px;
  }
}
@keyframes orderStepsArrowRight {
  from {
    margin-right: 23px;
  }
  to {
    margin-right: -8px;
  }
}
